:root{
    --font-family:'Poppins', sans-serif;
    --font-size-h1:5rem;
    --font-size-h2:1.5rem;
    --font-size-h3:2rem;
    --color-header:black;
    --color-body:white;
    --font-weight:bold;
}

.nb-ctn{
    width: 100%;
   height: 5vh;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;

}
.nb-itms{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    width: 100%;
  
}
.nb-itms > a {
    text-decoration: none;
    /* font-size: var(--font-size-h1); */
    color:white;
    color:var(--color-body);
    font-family: 'Poppins', sans-serif;
    font-family: var(--font-family);
    font-size: 1.5rem;
    font-size: var(--font-size-h2);

}
.img-sec{
    width: 100%;
    color:white;
    font-size: 3rem;
    padding-top: 20px;
    padding-left: 30px;
font-weight: bold;

}
.frm-ctn{
/* margin-top: 30px; */
width: 500px;
height: 600px;
background-color: white;
border-style: solid;
border-color: #cce7ff;
border-width: 3px;
border-radius: 20px;
margin-right: 100px;
}

.frm{
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content:center;
    grid-gap: 30px;
    gap: 30px;
    width: 100%;
}
.frm-crtl{
    width: 90%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-evenly;
    font-size:1.5rem;
    font-family: 'Poppins', sans-serif;
    padding-left: 30px;
}
.frm-crtl > input {
    border-style:solid;
    width: 100%;
    border-color:#cce7ff ;
    height: 2.5vh;
    border-radius: 10px;
    border-width: 2px;
}
.frm-crtl > textarea{
    border-style:solid;
    width: 100%;
    border-color:#cce7ff ;
    height: 20vh;
    border-radius: 10px;
    border-width: 2px;
}


*{
    font-family: 'Poppins', sans-serif;
}
.pg-ctn{
    width: auto;
    height: auto;
    /* background-color: rgba(209, 203, 203, 0.507); */
}
.backdrop-img{
    z-index: 3;
    position: absolute;
    top:30vh;
    left: 52vw;
    
}
svg{
    width: 30vw;
    height: 50vh;
}
.backdrop{
    position: absolute;
    z-index: -1;
    height:inherit;
    width: 100%;
    background:linear-gradient(#667eea ,#764ba2);
    -webkit-clip-path: polygon(0 0, 100% 0%, 100% 70%, 0 100%, 0% 50%);
            clip-path: polygon(0 0, 100% 0%, 100% 70%, 0 100%, 0% 50%);

}
.intro-hdr{
    height:80vh;
    width: 100%;
    z-index: 100;
    
    
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
 
}
.hero{
    width: 100%;
    display: flex;
    flex-direction: row;
    z-index: 1000;
    align-items: flex-start;    
    justify-content: space-evenly;
    margin-top:100px;
    
    
}
.intro-article{
  padding-left: 50px;
    display: flex;
    flex-direction:column;
    height: 100%;
    width: 100%;

}
.intro-article-hdr{
display: flex;
flex-direction: column;
font-size: 3rem;
}
.intro-main{
    font-size: 8rem;
    
}
.intro-article-desp{
    font-size:2rem;
    color:black;
    font-weight:bold;
    width: 50%;
}
.parent-feature{
    /* display: inline-block; */
    width: 100%;
}
.parent-feature img{
   width: 100%;
   padding-top: 30px;
   display: inline;
   transition: transform 0.2s;
}
.parent-feature img:hover{ 
  transform: scale(1.1);
}

