.data{
    box-shadow: violet;
}
.student-data{
    /* background-color:#6C63FF; */
    width: 70%;
    height: 40%;
    background-color: whitesmoke;
    box-shadow: 10px 10px #C1FFC1;
    border-radius: 10px;
}
.attribute-name{
    font-weight:bolder;
    font-family: 'Jura', sans-serif;
}
.ward-info{
    font-weight:lighter;
    font-size: smaller;
    font-family: 'Jura', sans-serif;
}
.edit-data{
    visibility: hidden;
    width: 120px;
}
.pencil-logo{
    margin-left: 0.5%;
    background-color:white ;
    width: 1.5%;
    height: 1.5%;
    margin-left: 10px;
}
.ward-info:hover .edit-data{
    visibility: visible;
    background-color:whitesmoke;
    font-size: 10px;
    position: relative;
    bottom:10px;
}
.details:hover{
    font-size: large;
}
.details{
    color: black;
}
.reports-heading{
    text-align: center;
    padding-left: 40%;
    text-decoration: underline;
}
.dropdown{
    width: 35%;
    display: inline !important;
}
.reports{
  /* padding-left: 10%; */
    height: 100%;
    width: 100%;
    display: flex;
}

.reports-graph{
  /* padding-left: 10%; */
    height: 50%;
    width: 50%;
    /* display: flex; */
}
.reports-option{
    padding-left: 40%;
    height: 50%;
    width: 50%;
    margin: 0%;
    /* display: none; */
}
.graphlist{
  display: none;
}
.reports canvas{

  width:100% !important;
  height:100% !important;
}

.dropdown{
  width: 80%;
}
.parents{
    display: flex;
    width: 100%;
}
.parents_feature{
    width: 7%;
    display: inline-block;
    background-color: whitesmoke;
    /* margin: 100px; */
}
.parents-feature-homescreen{
    /* display: flex; */
    width: 53%;
    margin-left: 2%;
}
.parents-feature-free_space{
    width: 40%;
    position: relative;
    bottom: 90px;
}
.parents_user-details{
    font-family: 'Jura', sans-serif;
    font-size: 20px;
    margin: 0;
}
.parents_user-details p{
    font-size: 20px;
}

.hover-object{
    
  visibility: hidden;
  /* width: 120px; */
  background-color: black;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;

  position: relative;
  left:5px;
}
.parents_user-details_welcome{
    font-weight: bolder;
}
.parents_user-details_welcome:hover .hover-object{
    background-color:whitesmoke;
    color: black;
    visibility: visible;
    font-size: small;
    position: relative;
    left:5px;
    bottom: 5px;
}

